	module.exports = {"data":{"talkJson":{"description":"Why is the overwhelming majority of common networked software still not secure, despite all effort to the contrary? Why is it almost certain to get exploited so long as attackers can craft its inputs? Why is it the case that no amount of effort seems to be enough to fix software that must speak certain protocols?\r\n\r\nThe answer to these questions is that for many protocols and services currently in use on the Internet, the problem of recognizing and validating their \"good\", expected inputs from bad ones is either not well-posed or is undecidable (i. e., no algorithm can exist to solve it in the general case), which means that their implementations cannot even be comprehensively tested, let alone automatically checked for weaknesses or correctness. The designers' desire for more functionality has made these protocols effectively unsecurable.\r\n\r\nIn this talk we'll draw a direct connection between this ubiquitous insecurity and basic computer science concepts of Turing completeness and theory of languages. We will show how well-meant protocol designs are doomed to their implementations becoming clusters of 0-days, and will show where to look for these 0-days. We will also discuss simple principles of how to avoid designing such protocols.","meta":{"duration":"PT59M23S"},"presenter":{"name":"Meredith L. Patterson"},"slug":"the-science-of-insecurity","source":{"id":"3kEfedtQVOY","provider":"youtube"},"tag":["28C3: Behind Enemy Lines","28th Chaos Communication Congress"],"title":"The Science of Insecurity"},"allTalkJson":null},"pathContext":{"slug":"the-science-of-insecurity","tags":["28C3: Behind Enemy Lines","28th Chaos Communication Congress"]}}

